.slider--home-hero {
    .slider-items {
        position: relative;

        .slider-item-inner {
            position: relative;

            .slider-content {
                position: absolute;
                bottom: 0;
                padding: 40px 20px;
                width: 100%;
                background-color: rgba(0, 0, 0, .5);
            }
        }
    }

    .flickity-page-dots {
        bottom: 10px;

        .dot {
            border-radius: 0;
            background: var(--colorP4)
        }
    }
}