body {
    background-image: url('../img/Sephirot-Background-Body.png');
    background-size: cover;
    background-position: center -205px;
    background-attachment: fixed;
    background-repeat: no-repeat;
}

#main {
    padding-top: 30vw;
    padding-bottom: 50px;
    color: var(--colorP4);

    .main-inner {
        max-width: 1920px;
        width: 90%;
        margin: 0 auto;
        display: flex;
        flex-wrap: wrap;
        padding: 0 50px;
        gap: 5%;
        background-color: var(--colorP1);
        box-shadow: 0 0 10px rgba(0, 0, 0, 1);
        position: relative;
        z-index: 1;

        .body-menu-wrapper {
            width: 100%;
            padding-bottom: 50px;

            .body-menu-inner {
                display: flex;
                justify-content: space-between;
                gap: 20px;
                padding: 20px 50px;
                margin: 0 -50px;
                background-color: var(--colorE5);
                position: relative;
                box-shadow: 0 10px 10px rgba($color: #000000, $alpha: .5);

                &:before {
                    content: '';
                    width: 100%;
                    height: 1px;
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    transform: translateX(-50%);
                    background: #000000;
                    background: var(--gradientBack);
                }

                &:after {
                    content: '';
                    width: 50%;
                    height: 3px;
                    position: absolute;
                    bottom: -1px;
                    left: 50%;
                    transform: translateX(-50%);
                    background: #000000;
                    background: var(--gradientGlow);
                    filter: blur(10px);
                }

                .body-menu {
                    display: flex;
                    flex-direction: row;
                    gap: 15px
                }
            }
        }

        .sections-wrapper {
            width: 65%;
            display: flex;
            flex-direction: column;
            gap: 50px;

            .main-section {

                &.section-news {
                    .content-inner {
                        padding: 2px;
                        background-color: var(--colorE5);
                        border-radius: 3px;
                        box-shadow: 0 10px 10px rgba(0, 0, 0, .8);

                        .title-box {
                            padding: 10px 20px;
                            position: relative;

                            &:before {
                                content: '';
                                width: 100%;
                                height: 1px;
                                position: absolute;
                                bottom: 0;
                                left: 50%;
                                transform: translateX(-50%);
                                background: #000000;
                                background: var(--gradientBack);
                            }

                            &:after {
                                content: '';
                                width: 50%;
                                height: 3px;
                                position: absolute;
                                bottom: -1px;
                                left: 50%;
                                transform: translateX(-50%);
                                background: #000000;
                                background: var(--gradientGlow);
                                filter: blur(10px);
                            }
                        }

                        .news-wrapper {
                            padding: 20px;
                            background-color: var(--colorS3);
                            border-radius: 0 0 3px 3px;
                        }
                    }
                }
            }
        }

        .sidebar-wrapper {
            width: 30%;
        }

        .section-footer {
            width: 100%
        }
    }
}

.main-section {
    min-height: 100px;
}