body {
    font-weight: var(--regular);
    font-family: var(--bodyFontFamily);
    font-size: var(--bodySize, 16px);
    line-height: 1.75;
}

h1,
h2,
h3,
h4,
h5,
h6,
.heading-element {
    font-family: var(--customHeadingFontFamily, var(--headingFontFamily));
    color: var(--headingColor);
    font-weight: var(--headingWeight, var(--regular));
    text-transform: var(--headingTransform, uppercase);
    line-height: var(--headingLineHeight, 1.1);
    letter-spacing: var(--headingLetterSpacing, 0px);

    @media only screen and (max-width: 767px) {
        font-size: var(--headingSizeMobile, var(--headingSize));
    }

    @media only screen and (min-width: 768px) and (max-width: 989px) {
        font-size: var(--headingSizeTablet, var(--headingSize));
    }

    @media only screen and (min-width: 990px) {
        font-size: var(--headingSize);
    }
}

h1,
.h1 .heading-element {
    --headingColor: var(--colorP1);
    --headingColorAlt: var(--colorP4);
    --headingWeight: var(--bold);
    --headingSize: 70px;
    --headingTabletSize: 65px;
    --headingMobilSize: 50px;
    --headingTransform: none;
}

h2,
.h2 .heading-element {
    --headingColor: var(--colorP1);
    --headingColorAlt: var(--colorP4);
    --headingWeight: var(--semibold);
    --headingSize: 56px;
    --headingTabletSize: 52px;
    --headingMobilSize: 40px;
    --headingTransform: none;
}

h3,
.h3 .heading-element {
    --headingColor: var(--colorP1);
    --headingColorAlt: var(--colorP4);
    --headingWeight: var(--semibold);
    --headingSize: 44px;
    --headingTabletSize: 40px;
    --headingMobilSize: 32px;
    --headingTransform: none;
}

h4,
.h4 .heading-element {
    --headingColor: var(--colorP1);
    --headingColorAlt: var(--colorP4);
    --headingWeight: var(--medium);
    --headingSize: 32px;
    --headingTabletSize: 30px;
    --headingMobilSize: 24px;
    --headingTransform: none;
}

h5,
.h5 .heading-element {
    --headingColor: var(--colorP1);
    --headingColorAlt: var(--colorP4);
    --headingWeight: var(--medium);
    --headingSize: 24px;
    --headingTabletSize: 22px;
    --headingMobilSize: 18px;
    --headingTransform: none;
}

h6,
.h6 .heading-element {
    --headingColor: var(--colorP2);
    --headingColorAlt: var(--colorP4);
    --headingWeight: var(--regular);
    --headingSize: 16px;
    --headingTabletSize: 16px;
    --headingMobilSize: 16px;
    --headingTransform: uppercase;
    --headingLetterSpacing: calc((1000 / var(--headingSize) * 50));
}

p {
    &:not(:last-child) {
        margin: 0 0 0.85em 0;
    }
}

.main-section {
    --sectionBgColor: var(--colorP4);
    --sectionColor: var(--colorP1);
    background-color: var(--sectionBgColor);
    color: var(--sectionColor);

    a {
        font-size: var(--linkSize, inherit);
        color: var(--linkColor, var(--colorP1));
        font-weight: var(--linkWeight, var(--regular));
        text-decoration: var(--linkDecoration, none);
        transition: var(--linkTransition, color ease-in-out 0.2s);

        &:hover {
            --linkColor: var(--linkColorHover, var(--colorP2));
            --linkDecoration: underline;
        }

        &:active {
            --linkColor: var(--colorP2);
            --linkDecoration: none;
        }
    }
}

.alternate-color {
    --sectionBgColor: var(--colorP1);
    --sectionColor: var(--colorP4);

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    .heading-element {
        color: var(--headingColorAlt, var(--headingColor));
    }

    a {
        --linkColor: var(--colorP4);
    }
}