/* Modern CSS Reset */

/* 1. Box sizing */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/* 2. Remove default margin */
* {
    margin: 0;
}

/* 3. Improve text rendering */
body {
    //min-height: 100vh;
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
}

/* 4. Media defaults */
img,
picture,
video,
canvas,
svg {
    display: block;
    max-width: 100%;
}

/* 5. Form elements */
input,
button,
textarea,
select {
    font: inherit;
}

/* 6. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
    overflow-wrap: break-word;
}

/* 7. Root stacking context */
#root,
#__next {
    isolation: isolate;
}

ul,
li {
    list-style: none;
    padding: 0;
    margin: 0;
}