.button {
    --c: #39e6ff;
    --bg: #05090c;
    --bg2: #0a141a;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 10px 18px;
    border-radius: 3px;
    text-decoration: none;
    letter-spacing: .08em;
    text-transform: uppercase;
    color: var(--c);
    background: linear-gradient(180deg, rgba(20, 40, 50, .35), rgba(0, 0, 0, .65)),
        radial-gradient(120% 140% at 50% 0%, rgba(57, 230, 255, .18), transparent 55%),
        linear-gradient(180deg, var(--bg2), var(--bg));

    border: 1px solid rgba(57, 230, 255, .35);
    box-shadow:
        inset 0 1px 0 rgba(255, 255, 255, .08),
        inset 0 -2px 8px rgba(0, 0, 0, .75),
        0 0 0 1px rgba(0, 0, 0, .55);
    text-shadow:
        0 0 6px rgba(57, 230, 255, .55),
        0 0 14px rgba(57, 230, 255, .22);
    transition: transform .12s ease, box-shadow .2s ease, border-color .2s ease, filter .2s ease;

    &:hover {
        border-color: rgba(57, 230, 255, .55);
        box-shadow:
            inset 0 1px 0 rgba(255, 255, 255, .1),
            inset 0 -2px 8px rgba(0, 0, 0, .7),
            0 0 10px rgba(57, 230, 255, .25);
        filter: brightness(1.08);
    }

    &:active {
        transform: translateY(1px);
        box-shadow:
            inset 0 2px 10px rgba(0, 0, 0, .85),
            0 0 6px rgba(57, 230, 255, .15);
    }

    &:focus-visible {
        outline: none;
        box-shadow:
            0 0 0 3px rgba(57, 230, 255, .18),
            inset 0 1px 0 rgba(255, 255, 255, .08),
            inset 0 -2px 8px rgba(0, 0, 0, .75);
    }
}