:root {
    $burker-breakpoint: 990px;
    --headerMain: 113px;
    --headerNav: calc(var(--headerMain));

    //Menu Items
    --menuItemColor: var(--colorP4);
    --menuItemSize: 18px;
    --menuStatus: block;
    --headerToggleButtonStatus: none;
    --menuGap: 40px;

    @media only screen and (max-width: #{$burker-breakpoint}) {
        --menuStatus: none;
        --headerToggleButtonStatus: block;
    }

    .header {
        position: fixed;
        width: 100%;
        z-index: 9999;
        background-color: var(--headerInnerBg, transparent);
        box-shadow: var(--headerShadow, none);
        transition: background-color ease .3s, box-shadow ease .3s;

        &:before,
        &:after {
            content: '';
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            background: #000000;
            transition: width ease .3s;
        }

        &:before {
            width: var(--headerBackLine, 0);
            height: 1px;
            bottom: 0;
            background: var(--gradientBack);
        }

        &:after {

            width: var(--headerGradientLine, 0);
            height: 3px;
            bottom: -1px;
            background: var(--gradientGlow);
            filter: blur(10px);
        }

        .header-inner {
            height: var(--headerMain);
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            padding: 0 50px;
            margin: 0 auto;
            position: relative;
            gap: 20px;
            transition: height ease .3s;

            @media only screen and (min-width: #{$burker-breakpoint+1}) {
                max-width: var(--container);
            }

            @media only screen and (max-width: #{$burker-breakpoint}) {
                justify-content: space-between;
                padding: 0 20px;
            }

            &>* {
                position: relative;
            }

            .header-logo {
                a {
                    position: relative;
                    width: 100%;
                    display: flex;
                    aspect-ratio: 1036 / 609;
                    max-width: 232px;
                    height: 113px;
                    justify-content: center;
                    align-items: center;

                    img {
                        position: absolute;
                        width: var(--logoSize, 100%);
                        height: var(--logoSize, 100%);
                        object-fit: cover;
                        object-position: center center;
                        transition-property: width, height;
                        transition-duration: .3s;
                        transition-timing-function: ease-in-out;
                    }

                }
            }

            .header-menu {
                display: var(--menuStatus);
                width: 25%;
                height: 100%;

                .main-menu {
                    align-items: center;
                    flex-direction: row;
                    display: flex;
                    height: 100%;
                    margin: 0 calc(var(--menuGap) * -1);

                    &>.menu-item:not(.menu-item-button) {
                        position: relative;
                        height: 100%;

                        &>.menu-item-inner {
                            height: 100%;
                            color: var(--menuItemColor);
                            font-size: var(--menuItemSize);
                            text-decoration: none;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            padding: 0 var(--menuGap);
                            transition: text-shadow ease .3;
                            text-shadow: 0 0 10px var(--colorP1);

                            .menu-item-text {
                                position: relative;

                                &:before {
                                    content: '';
                                    width: 150%;
                                    height: 110%;
                                    position: absolute;
                                    left: 50%;
                                    top: 50%;
                                    background-color: var(--colorP2);
                                    border-radius: 100%;
                                    transform: translateY(-50%) translateX(-50%);
                                    filter: blur(10px);
                                    opacity: 0;
                                    transition: opacity ease .3s;
                                }
                            }

                            &:hover {
                                .menu-item-text {
                                    &::before {
                                        opacity: .1;
                                    }
                                }
                            }
                        }

                        &:not(:last-child) {
                            &:before {
                                content: "";
                                width: 2px;
                                height: 40%;
                                position: absolute;
                                right: 0;
                                top: 50%;
                                transform: translateY(-50%);
                                background-color: VAR(--colorE7);
                                opacity: 1;
                                z-index: 2;
                            }

                            &:after {
                                content: "";
                                width: 1px;
                                height: 40%;
                                position: absolute;
                                right: -1px;
                                top: calc(50% - -2px);
                                transform: translateY(-50%);
                                background-color: var(--colorP4);
                                opacity: .1;
                            }
                        }
                    }
                }

                &.header-menu-left {
                    .main-menu {
                        justify-content: flex-end;
                    }
                }
            }

            .hamburger-toggle {
                width: 30px;
                height: 19px;
                padding: 0;
                position: relative;
                background: transparent;
                border: none;
                outline: none;
                border-radius: 0;
                cursor: pointer;
                display: var(--headerToggleButtonStatus);

                .burger-box {
                    width: 100%;
                    height: 3px;
                    background-color: var(--toggleColor, var(--colorP4));
                    transition-property: background-color;
                    transition-timing-function: ease-in-out;
                    transition-duration: 0.2s;

                    &::before,
                    &::after {
                        content: '';
                        height: 3px;
                        width: 100%;
                        background-color: var(--toogleColor, var(--colorP4));
                        position: absolute;
                        transition-property: background-color, opacity, transform, top, left, bottom, right;
                        transition-timing-function: ease-in-out;
                        transition-duration: 0.2s;
                    }

                    &::before {
                        top: 0;
                        left: 0;
                    }

                    &::after {
                        bottom: 0;
                        left: 0;
                    }
                }


            }
        }
    }

    &.header-is-scrolled {
        --headerInnerBg: var(--colorE5);
        --headerShadow: 0 0 10px rgba(0, 0, 0, 1);
        --logoSize: 80%;
        --headerMain: 96px;
        --headerGradientLine: 50%;
        --headerBackLine: 100%;
    }
}