.columns-element {
    --columnGap: 60px;

    .text-center {
        text-align: center;
    }

    .columns-element-inner {
        display: flex;
        flex-wrap: wrap;
        margin: 0 calc((var(--columnGap) / 2)* -1);

        .column {
            width: 50%;
            padding: 0 calc(var(--columnGap) / 2);

            .panel-sidebar {
                padding: 2px;
                background-color: var(--colorE5);
                box-shadow: 0 0 5px var(--colorP1);
                border-radius: 3px;
                box-shadow: 0 10px 10px rgba(0, 0, 0, .8);

                .panel-heading {
                    padding: 10px 20px;
                    position: relative;
                    text-align: center;
                    text-shadow: 0 -4px 2px #000;
                    position: relative;

                    &:before {
                        content: "";
                        width: 100%;
                        height: 1px;
                        position: absolute;
                        bottom: 0;
                        left: 50%;
                        transform: translateX(-50%);
                        background: #000;
                        background: var(--gradientBack);
                    }

                    &:after {
                        content: "";
                        width: 50%;
                        height: 3px;
                        position: absolute;
                        bottom: -1px;
                        left: 50%;
                        transform: translateX(-50%);
                        background: #000;
                        background: var(--gradientGlow);
                        filter: blur(10px);
                    }

                    .panel-title {
                        font-size: 16px;
                        text-align: center;
                    }
                }

                .panel-body {
                    padding: 20px;
                    background-color: var(--colorS3);
                    border-radius: 0 0 3px 3px;

                    table {
                        width: 100%;
                        text-align: center;
                    }
                }
            }
        }
    }
}