.section-footer {
    background: transparent;
    padding-top: 50px;

    .footer-inner {
        margin: 0 -50px;
        padding: 50px;
        background-color: var(--colorE5);
        color: var(--colorP4);
        text-align: center;
        text-shadow: 0 4px 10px rgba($color: #000000, $alpha: 1.0);
        position: relative;

        &:before {
            content: "";
            width: 100%;
            height: 1px;
            position: absolute;
            top: 0;
            left: 50%;
            transform: translateX(-50%);
            background: #000;
            background: var(--gradientBack);
        }

        &:after {
            content: "";
            width: 50%;
            height: 3px;
            position: absolute;
            top: -1px;
            left: 50%;
            transform: translateX(-50%);
            background: #000;
            background: var(--gradientGlow);
            filter: blur(10px);
        }

        .footer-hour {
            display: flex;
            justify-content: space-around;
            gap: 20px;
            position: relative;
            margin-top: 30px;
            padding-top: 30px;

            &:before {
                content: "";
                width: 100%;
                height: 1px;
                position: absolute;
                top: 0;
                left: 50%;
                transform: translateX(-50%);
                background: #000;
                background: linear-gradient(90deg, rgba(0, 0, 0, 0) -10%, #fff 50%, rgba(0, 0, 0, 0) 110%);
            }

            &>div {
                display: flex;
                flex-direction: column;
                justify-content: center;

                span {
                    color: var(--colorP2);
                    font-weight: var(--bold);
                    font-size: 40px;
                }
            }
        }
    }
}