.news-wrapper {
    .panel-news {
        position: relative;

        .panel-heading {
            position: relative;
            padding-bottom: 20px;
            margin-bottom: 20px;

            &:before {
                content: "";
                width: 100%;
                height: 1px;
                position: absolute;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                background: #000;
                background: linear-gradient(90deg, rgba(0, 0, 0, 0) -10%, #fff 50%, rgba(0, 0, 0, 0) 110%);
            }

            .panel-title {
                font-size: 30px;
            }
        }

        &:not(:last-child) {
            padding-bottom: 35px;
            margin-bottom: 35px;

            &:before {
                content: "";
                width: 100%;
                height: 1px;
                position: absolute;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                background: #000;
                background: var(--gradientBack);
            }

            &:after {
                content: "";
                width: 50%;
                height: 3px;
                position: absolute;
                bottom: -1px;
                left: 50%;
                transform: translateX(-50%);
                background: #000;
                background: var(--gradientGlow);
                filter: blur(10px);
            }
        }
    }
}