.sidebar-wrapper {

    .sidebar-blocks {
        display: flex;
        flex-direction: column;
        gap: 20px;

        .sidebar-block {
            .sidebar-block-inner {
                padding: 2px;
                background-color: var(--colorE5);
                box-shadow: 0 0 5px var(--colorP1);
                border-radius: 3px;
                box-shadow: 0 10px 10px rgba(0, 0, 0, .8);

                .block-title {
                    padding: 10px 20px;
                    position: relative;
                    text-align: center;
                    text-shadow: 0 -4px 2px black;

                    &:before {
                        content: '';
                        width: 100%;
                        height: 1px;
                        position: absolute;
                        bottom: 0;
                        left: 50%;
                        transform: translateX(-50%);
                        background: #000000;
                        background: var(--gradientBack);
                    }

                    &:after {
                        content: '';
                        width: 50%;
                        height: 3px;
                        position: absolute;
                        bottom: -1px;
                        left: 50%;
                        transform: translateX(-50%);
                        background: #000000;
                        background: var(--gradientGlow);
                        filter: blur(10px);
                    }
                }

                .block-content {
                    padding: 20px;
                    background-color: var(--colorS3);
                    border-radius: 0 0 3px 3px;

                    .block-content-block {
                        display: flex;
                        flex-wrap: wrap;
                        justify-content: space-between;
                        align-items: center;
                        position: relative;

                        &:not(:last-child) {
                            padding-bottom: 10px;
                            margin-bottom: 10px;

                            &:before {
                                content: '';
                                width: 100%;
                                height: 1px;
                                position: absolute;
                                bottom: 0;
                                left: 50%;
                                transform: translateX(-50%);
                                background: #000000;
                                background: linear-gradient(90deg, rgba(0, 0, 0, 0) -10%, rgba(255, 255, 255, 1) 50%, rgba(0, 0, 0, 0) 110%);
                            }
                        }

                        .online-players-bar {
                            width: 100%;
                            position: relative;
                            height: 17px;
                            border: 1px solid rgba(255, 255, 255, 0.1);
                            border-radius: 4px;
                            box-sizing: border-box;
                            margin-top: 20px;

                            .webengine-online-bar-progress {
                                position: absolute;
                                left: 0;
                                height: 100%;
                                border-radius: inherit;
                                background: linear-gradient(-45deg, #02d2f3, transparent);
                            }
                        }

                    }
                }
            }

            &.sidebar-block-events {
                .panel-sidebar-events {
                    width: 100%;

                    .panel-heading {
                        display: none;
                    }

                    .table-condensed {
                        width: 100%;

                        tbody {
                            tr {
                                display: flex;
                                flex-direction: row;
                                justify-content: space-between;
                                position: relative;

                                td.text-right {
                                    text-align: right;
                                }

                                &:not(:last-child) {
                                    padding-bottom: 10px;
                                    margin-bottom: 10px;

                                    &:before {
                                        content: '';
                                        width: 100%;
                                        height: 1px;
                                        position: absolute;
                                        bottom: 0;
                                        left: 50%;
                                        transform: translateX(-50%);
                                        background: #000000;
                                        background: linear-gradient(90deg, rgba(0, 0, 0, 0) -10%, rgba(255, 255, 255, 1) 50%, rgba(0, 0, 0, 0) 110%);
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}