@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');

html {
    --scrollBarWidth: 0px;
    --container: var(--containerWidth, 80%);
    --defaultStrong: var(--bold);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    @media only screen and (min-width: 768px) {
        --container: var(--containerWidth, 1310px);
    }
}

:root {

    //Colors
    --colorBlack: #000;
    --colorWhite: #fff;

    //Primary
    --colorP1: #171717;
    --colorP2: #02bcd1;
    --colorP3: #0E0E0E;
    --colorP4: var(--colorWhite);

    //Secondary
    --colorS1: #5D67B1;
    --colorS2: #5D9FB1;
    --colorS3: #0f1014;
    --colorS4: #999999;
    --colorS5: #6f635b;
    --colorS6: #9c8474;

    //Extra
    --colorE1: #037885;
    --colorE2: #060606;
    --colorE3: #51B208;
    --colorE4: #a50808;
    --colorE5: #2D3034;
    --colorE6: #02d2f3;
    --colorE7: #1e1f20;

    //Font Family
    --headingFontFamily: 'Roboto', 'sans-serif';
    --bodyFontFamily: 'Roboto', 'sans-serif';

    // Font Weight
    --light: 300;
    --regular: 400;
    --medium: 500;
    --semibold: 600;
    --bold: 700;

    --gradientBack: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, rgba(2, 210, 243, 1) 51%, rgba(0, 0, 0, 0) 100%);
    --gradientGlow: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, rgba(2, 210, 243, 1) 51%, rgba(0, 0, 0, 0) 100%);
}